import React, { Component } from 'react';
import { debounce } from './debounce'
const url = `https://search-instant.herokuapp.com/search?q=`//`http://localhost:5000/search?q=`//

class Search extends Component {
	constructor(props) {
		super(props)
		this.state = {
			data: [],
			time: '~',
			isDebounced: false
		}
		this.debouncer = debounce(this._onChange, 500)
	}

	_onChange = (value) => {
		if(value.length >= 3){
			fetch(`${url}${value}`)
				.then(res => res.json())
				.then(({ data }) => {
					if(data) {
						this.setState({ data })
					}
				})
		} else {
			this.setState({ data: [] })
		}
	}

	checkDebounce = e => {
		this.setState({isDebounced: e.target.checked})
	}

	render() {
		console.log(this.state.data.length)
		return (
			<div className="Search">
				<h2>Search API</h2>
				<input onChange={this.state.isDebounced ? this.debouncer : (e) => this._onChange(e.target.value)} placeholder="Search for a name" />
				<br/>
				<h4><span><input style={{width: 'auto'}} type="checkbox" onChange={this.checkDebounce}/> Debounce</span> | {this.state.data.length} results</h4>
				{this.state.data.map((d) => {
					return <div className="searchItem" key={d.value}><p>{d.value}</p><p>{d.score}</p></div>
				})}
			</div>
		);
	}
}

export default Search;
