export function debounce(func, wait, immediate) {
	var timeout;
	return function(e) {
        var context = this, value = e && e.target.value.slice();
		var later = function() {
			timeout = null;
			if (!immediate) func.apply(context, [value]);
		};
		var callNow = immediate && !timeout;
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
		if (callNow) func.apply(context, [value]);
	};
};